import React from 'react'

export default class header extends React.Component{
	constructor(props){
    super(props)
    this.state={
      show:false
    }
    this.btnClick = this.btnClick.bind(this)
  }
  btnClick(){
    this.setState({
      show:!this.state.show
    })
  }
  render(){
		return(
				<div>
<nav className="navbar navbar-expand-lg navbar-dark fixed-top mb-2">
  <a className="navbar-brand" href="#"><h3 className="header-head ml-1 pt-2">UPSC Current</h3></a>
    <span className="navbar-nav nav-ul text-right">
        <a className="nav-link">Prelims Current Affair's</a>
        <a className="nav-link">Motivation</a>
        <a className="nav-link">Study Material</a>
        <a className="nav-link">Contact Us</a>
      </span>
    <button className="btn-nav justify-content-center mt-0" onClick={this.btnClick}>
      <ul >
      <li className="ml-0 span1"></li>
      <li className="span1 ml-0 "></li>
      </ul>
    </button>
</nav>
        {this.state.show ?
        <div className="nav-dialog mt-5">
            <br/><br/><br/>
            <ul className="pt-3">
              <li>Prelims Current Affair's</li>
              <hr/>
              <li>Motivation</li>
              <hr/>
              <li>Study Material</li>
              <hr/>
              <li>Contact Us</li>
              <hr/>
            </ul>
        </div>
        :
        <span></span>}
				</div>
			)
	}
}